.TodoListItem-virtualized {
  & + & {
    border-top: 1px solid #ffecb3;
  }

  &:nth-child(even) {
    background: #f8f9fa;
  }
}

.TodoListItem {
  padding: 1em;
  display: flex;
  align-items: center; // 세로 중앙 정렬
  background: #7eb2ed;

  //   &:nth-child(even) {
  //     background: #e9f2fd;
  //   }

  .checkbox {
    color: #ffecb3;
    cursor: pointer;
    flex: 1; // 차지할 수 있는 영역 모두 차지
    display: flex;
    align-items: center; // 세로 중앙 정렬

    svg {
      // 아이콘
      font-size: 1.5rem;
    }

    .text {
      margin-left: 0.5rem;
      flex: 1; // 차지할 수 있는 영역 모두 차지
    }

    // 체크되었을 때 보여 줄 스타일
    &.checked {
      svg {
        color: #ffecb3;
      }
      .text {
        color: #ffecb3;
        text-decoration: line-through;
      }
    }
  }

  .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ffecb3;
    cursor: pointer;

    &:hover {
      color: #f55477;
    }
  }
}

@media screen and (max-width: 768px) {
  .TodoListItem {
    width: 320px;
    padding: 1em;
    display: flex;
    align-items: center; // 세로 중앙 정렬
  }
}
